// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 保养任务service
import ZTree from '@/components/common/ZTree/ZTree';
// 公共方法service
// 文字常量
// 引入table组件
import VXETable from '@/components/common/Table';
// API
import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  PAGE_TITLE,
  ENERGY,
  MAINTAINTASK_STATUS,
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE
  // FILE_TYPE
} from '@/utils/constantsFile';
// API
import {
  // selectFile,
  getAllGroupAndDevice
  // REPAIR_FILE,
  // downloadFile,
  // deleteFileById
} from '@/api/equipment';
import { listPlannedProduction, insertPlannedProduction, updatePlannedProduction } from '@/api/xwReport';
// import { BASE_URL } from '@/utils/ajaxConfigure';
// import { exportMaintainTask } from '@/api/export';
// message service
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 所搜框
import SearchBox from '@/components/common/SearchBox';
// 提示信息
import { showErrorNotify } from '@/utils/notify';
import moment from 'moment';
export default {
  name: 'MaintainceTask',
  components: {
    ZTree,
    HeaderTitle,
    HeadOperationItem,
    SearchBox,
    VXETable
  },
  inject: ['reload'],
  data() {
    return {
      VARIABLE: VARIABLE,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      // 弹窗提示
      warmInfo: '',
      nodeData: [],
      // 查询字段
      // 加载表数据标识
      tableData: [],
      tableLoading: false,
      isCanUpload: true,
      titleText: '新增',
      dialogVisible: false,
      plannedProduction: 0,
      plannedRate: 0,
      cycleTime: 0,
      btnType: 0,
      planRow: {},
      value2: '',
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      }
    };
  },
  methods: {
    /**
     * @description: 获取树结构
     * @return: void
     */
    getTree() {
      getAllGroupAndDevice().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < this.nodeData.length; i++) {
            if (this.nodeData[i].code) {
              this.selectData = this.nodeData[i];
              break;
            }
          }
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    add() {
      this.titleText = '新增';
      this.dialogVisible = true;
      this.plannedProduction = 0;
      this.plannedRate = 0;
      this.btnType = 0;
      this.value2 = moment(new Date().getTime()).format('YYYY-MM-DD');
    },
    toSubmit() {
      if (this.btnType === 0) {
        insertPlannedProduction({}, {
          deviceCode: this.treeSelectCode,
          deviceName: this.treeSelectCode,
          plannedProduction: this.plannedProduction,
          cycleTime: this.cycleTime,
          plannedRate: this.plannedRate,
          recordTime: this.value2 + ' 00:00:00'
        })
          .then(res => {
            if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
              // this.tableData = res.rows;
              showSuccessMessage('操作成功');
              this.getFilesData();
            } else {
              showWarningMessage(res.message);
            }
            this.dialogVisible = false;
          });
      } else {
        updatePlannedProduction({}, {
          id: this.planRow.id,
          plannedProduction: this.plannedProduction,
          plannedRate: this.plannedRate,
          cycleTime: this.cycleTime
        })
          .then(res => {
            if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
              // this.tableData = res.rows;
              showSuccessMessage('操作成功');
              this.getFilesData();
            } else {
              showWarningMessage(res.message);
            }
            this.dialogVisible = false;
          });
      }
    },
    /**
     * @description: 根据url下载文件
     * @param: {String} url 文件url
     * @return: void
     */
    async update(row) {
      this.planRow = row;

      this.titleText = '修改';
      this.dialogVisible = true;
      this.plannedProduction = row.plannedProduction;
      this.plannedRate = row.plannedRate;
      this.cycleTime = row.cycleTime;
      this.btnType = 1;
    },
    /**
     * @description: 树点击事件
     * @param {object} data 树结构渲染数据
     * @param {object} treeNode 树结构真实数据
     * @return: void
     */
    handleNodeClick(data, treeNode) {
      console.log(treeNode);
      if (treeNode.code) {
        this.treeSelectCode = treeNode.code;
        this.treeSelectGroup = '';
        // this.uploadConfig.data.relationshipId = Number(treeNode.code.split('_')[1]);
        this.isCanUpload = false;
        // this.$refs.headButtons.showBtn = ['2', '3', '4', '5', '6', '7'];
        // this.selectName = treeNode.code;
        // this.selectCode = treeNode.code;
        // this.showGroup = true;
        // this.toolListTable.page.currentPage = 1;
        // this.toolListTable.page.pageSize = 10;
        // this.getToolListData(treeNode.code);
      } else {
        this.treeSelectCode = '';
        this.treeSelectGroup = treeNode.id;
        this.uploadConfig.data.relationshipId = '';
        this.isCanUpload = true;
        // console.log(treeNode);
        showWarningMessage('请选择设备！');

        // this.treeSelectCode =
        // this.$refs.headButtons.showBtn = ['1', '2', '3', '4', '5', '6', '7', '8'];
        // showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
      }
      this.getFilesData();
    },
    getFilesData() {
      this.tableData = [];
      listPlannedProduction({
        keyWord: this.treeSelectCode,
        pageNum: 1,
        pageSize: 99999999
      }, {})
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            this.tableData = res.rows;
          }
        });
    }
  },
  beforeCreate() {
  },
  computed: {
  },
  mounted() {
  },
  created() {
    this.getTree();
  }
};
